import React from 'react';
import translate from '../i18n/messages/translate'
import { useNavigate } from "react-router-dom"
import { Form, Button, Container, Row, Col} from 'react-bootstrap'
import TitleInputGroup from './subComponents/TitleInputGroup'
import TypeInputGroup from './subComponents/TypeInputGroup'
import AuthorsInputGroup from './subComponents/AuthorsInputGroup'
import DateInputGroup from './subComponents/DateInputGroup'
import PublisherInputGroup from './subComponents/PublisherInputGroup'
import FileInputGroup from './subComponents/FileInputGroup'
import LinkInputGroup from './subComponents/LinkInputGroup'
import CitationsInputGroup from './subComponents/CitationsInputGroup'
import DescriptionInputGroup from './subComponents/DescriptionInputGroup'

const AddProduct = () => {
    const navigate = useNavigate()
    const editItem = () => {
        navigate('/profile')
    }
    return (
        <div className='product-page'>
            <div className='product-text'>Edit An Academic Product</div>
            <Form onSubmit={editItem} className="justify-content-around text-capitalize product-form p-5">
                <Container className="d-flex flex-column align-items-center">
                    <Row className="w-100 justify-content-between">
                        <Col className="col-6">
                            <TitleInputGroup/>
                            <TypeInputGroup/>
                            <AuthorsInputGroup />
                            <DateInputGroup/>
                            <PublisherInputGroup/>
                            <FileInputGroup/>
                        </Col>
                        <Col className="col-6">
                            <LinkInputGroup/>
                            <CitationsInputGroup/>
                            <DescriptionInputGroup/>
                        </Col>
                    </Row >
                    <Row className="mt-3 mb-0 w-50 d-flex justify-content-center">
                        <Form.Group className="mx-3">
                            <Button type="submit" className ="arved-button3">
                                {translate("Edit")}
                            </Button>
                        </Form.Group>
                        <Form.Group className="mx-3">
                            <Button type="submit" className ="arved-button4">
                                {translate("cancel")}
                            </Button>
                        </Form.Group>
                    </Row>
                </Container>    
            </Form>  
        </div> 
    );
}
export default AddProduct