import React, { useEffect } from 'react'
import AddProductIcon from '../iconComponents/icons/AddIcon'
import Product from './Product'
import { Table } from 'react-bootstrap'
import { useSelector, useDispatch } from 'react-redux'
import { getAllProducts } from '../reducers/productReducer'
import { useNavigate } from "react-router-dom"
import translate from '../i18n/messages/translate'

const Products = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(getAllProducts())
    }, [dispatch])
    const products = useSelector(state => state.products)

    return (
        <div>
            <div className='d-flex justify-content-end my-3'>
                <button
                    className='arved-button2 d-flex align-items-center'
                    style={{ backgroundColor: "transparent" }}
                    onClick={() => navigate('/add-product')}
                >
                    <AddProductIcon className='mx-1' />
                    <span>{translate(`add-a-product`)}</span>
                </button>
            </div>
            <Table className='d-flex flex-column'>
                <thead className='d-flex product-bar mb-3'>
                    <tr className='d-flex align-items-center w-100'>
                        <th className='ml-3 product-section'>{translate(`product`)}</th>
                        <th className='product-type-year d-flex justify-content-between'>
                            <span className=''> {translate(`type`)}</span>
                            <span className=''> {translate(`year`)}</span>
                        </th>
                    </tr>
                </thead>
                <tbody className='products text-capitalize'>
                    {products !== [] ?
                        products.map(product => <Product key={product.id} product={product} />)
                        : null
                    }
                </tbody>
            </Table>
        </div>
    )
}

export default Products