import React, { useState } from 'react';

import { Form, InputGroup } from 'react-bootstrap'
import { FormattedMessage } from 'react-intl'
import AuthorIcon from '../../iconComponents/icons/AuthorIcon'
import AddIcon from '../../iconComponents/icons/AddIcon'

const AuthorsInputGroup = () => {
    const [author, setAuthor] = useState('')
    const [authors, setAuthors] = useState([])

    const onAuthorChange = (event) => {
            setAuthor(event.target.value)
    }

    const addAuthor = () => {
        //do not add authors with only whitespaces
        if (/\S/.test(author))
            setAuthors(authors.concat(author))
        setAuthor('')
    }
    return (
        <div>
            {authors.length > 0 ? 
            <div className='text-info'>Authors: {authors.join(', ')}</div> : null}
            <InputGroup className="arved-input-group">
                <button
                    type="button"
                    className="no-style"
                    onClick={addAuthor} >
                    <AddIcon />
                </button>
                <FormattedMessage id="authors" >
                    {placeholder => <Form.Control  
                        className= "arved-input-label2"
                        type="text"
                        name="authors"
                        value= {author}
                        onChange={event => onAuthorChange(event)}
                        placeholder={placeholder}               
                    />
                    }
                </FormattedMessage>
                <AuthorIcon/>
            </InputGroup>
        </div>
    )
}
export default AuthorsInputGroup