import EditProductIcon from '../iconComponents/icons/EditProductIcon'
import DeleteProductIcon from '../iconComponents/icons/DeleteProductIcon'
import translate from '../i18n/messages/translate'

const Product = ({product}) => {
    return (
        <tr key={product.id} className='mb-3 product d-flex align-items-center'>
            <td className='ml-3 product-section'>
                <p className='product-title'>{product.title}</p>
                <p className='product-author'>{product.authors.join(', ')}</p>
                <p className='product-magazine'>The Journal of Supercomputing</p>
            </td>
            <td className='product-type-year d-flex justify-content-between'>
                <span> {translate(`${product.type}`)}</span>
                <span> {new Date(product.publication_date).getFullYear()}</span>
            </td>
            <td className='d-flex justify-content-between'>                                
                <button className='arved-button2 d-flex align-items-center'>
                    <EditProductIcon/>
                </button>
                <button className='arved-button2 d-flex align-items-center'>
                    <DeleteProductIcon />
                </button>
            </td>
        </tr>
    )
}

export default Product