import { Form, InputGroup} from 'react-bootstrap'
import { FormattedMessage } from 'react-intl'
import ListIcon from '../../iconComponents/icons/ListIcon'

const TypeInputGroup = () => {
    return (
    <InputGroup className="justify-content-end arved-input-group">
        <FormattedMessage id="type of academic product" >
            {placeholder => <Form.Select className="select-group  " name ="type">
                <option>{placeholder}</option>
                <option value="project" className="bg-light text-center">project</option>
                <option value="article" className="bg-light text-center">article</option>
                <option value="conference paper" className="bg-light text-center">conference paper</option>
                <option value="book chapter" className="bg-light text-center">book chapter</option>
            </Form.Select>
            }
        </FormattedMessage>
        <ListIcon/>
    </InputGroup>  
    )
}
export default TypeInputGroup