import DateIcon from '../../iconComponents/icons/DateIcon'
import DatePickerIcon from '../../iconComponents/icons/DateIcon'

import { FormattedMessage } from 'react-intl'
import { Form, InputGroup} from 'react-bootstrap'

const DateInputGroup = () => {
    return (
        <InputGroup className="arved-input-group">
            <DatePickerIcon/>
                <FormattedMessage id="publication date">
                    { placeholder => <Form.Control
                        className= "arved-input-label2"
                        type='date'
                        name= "publication_date"
                        placeholder={placeholder}
                        />
                    }
                </FormattedMessage>
            <DateIcon/>
        </InputGroup>
    )
}

export default DateInputGroup